import { errorTip, shortcut } from '@engine/core';
import { ReportChartGamma } from './chart_gamma';

@shortcut()
export class ReportChartDelta extends ReportChartGamma {
    public static xtype = 'report.chart_delta';

    public static VERSION = '2.0.0';

    public static BUILD_TIME = '';

    public resize(wScale: number, hScale: number, fontScale: number) {
        if (hScale && wScale) {
            this.options.wScale = wScale;
            this.options.hScale = hScale;
            this.options.fontScale = fontScale;
            this.chart?.setAutoFitScale(fontScale, wScale, hScale);
        }
    }

    protected enforceChartAttr() {
        const { wScale, hScale, fontScale } = this.options;
        if (this.chartAttr?.chartAttr) {
            this.chartAttr.chartAttr.wScale = wScale;
            this.chartAttr.chartAttr.hScale = hScale;
            this.chartAttr.chartAttr.fontScale = fontScale;
        }
    }

    public refresh() {
        this.chart?.setAutoFitScale(this.options.fontScale, this.options.wScale, this.options.hScale);
    }

    // 新版本图表不需要创建前resetGlobalScale
    @errorTip()
    public render() {
        return {
            type: BI.AbsoluteLayout.xtype,
        };
    }
}
